/* Spinner */
.App_spinner__1E3SE {
    width: 60px;
    height: 60px;
    margin: 100px auto;
    background-color: #ff6600;
  
    border-radius: 100%;  
    -webkit-animation: App_sk-scaleout__1GleI 1.0s infinite ease-in-out;
    animation: App_sk-scaleout__1GleI 0.7s infinite ease-in-out;
  }
  
  @-webkit-keyframes App_sk-scaleout__1GleI {
    0% { -webkit-transform: scale(0) }
    100% {
      -webkit-transform: scale(1.0);
      opacity: 0;
    }
  }
  
  @keyframes App_sk-scaleout__1GleI {
    0% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 100% {
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
      opacity: 0.1;
    }
  }
